html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  width: 100%;
  font-family: "Montserrat", sans-serif;
  background-color: #181719;
  color: #FFF;
}

a {
  display: block;
  text-decoration: none;
  color: #FFF;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

@media (min-width: 1024px) {
  .contenedor {
    width: 80%;
  }
}
.text-center {
  text-align: center;
}

.visibilidad-none {
  visibility: hidden;
  opacity: 0;
}

.m-0 {
  margin: 0;
}

.hamburger {
  padding: 0;
}
.hamburger.is-active .hamburger-inner {
  background-color: #FFF;
  transition: all 0.3s;
}
.hamburger.is-active .hamburger-inner::after, .hamburger.is-active .hamburger-inner::before {
  background-color: #FFF;
  transition: all 0.3s;
}
.hamburger .hamburger-inner {
  background-color: #FFF;
  transition: all 0.3s;
}
.hamburger .hamburger-inner::after, .hamburger .hamburger-inner::before {
  background-color: #FFF;
  transition: all 0.3s;
}

.header {
  margin-top: 2rem;
  padding: 0.1px;
  position: relative;
}
.header .header__hamburguer {
  position: absolute;
  z-index: 999;
  right: 2.4rem;
  top: 1rem;
}
@media (min-width: 768px) {
  .header .header__hamburguer {
    display: none;
  }
}

.header__contenedor {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 998;
}
@media (min-width: 768px) {
  .header__contenedor {
    flex-direction: row;
    height: auto;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .header__contenedor {
    flex-direction: row;
  }
}
.header__contenedor .header__marca h1 {
  font-family: "Lora", serif;
  font-weight: 200;
  font-size: 2rem;
  margin: 0;
  border: 1px solid #FFF;
  padding: 1rem;
  transition: all 0.3s;
}
.header__contenedor .header__marca {
  align-self: start;
}
.header__contenedor .header__menu {
  display: flex;
  justify-content: center;
}
.header__contenedor .header__menu .header__links {
  position: absolute;
  top: 22rem;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  text-align: center;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s;
}
@media (min-width: 768px) {
  .header__contenedor .header__menu .header__links {
    visibility: visible;
    opacity: 1;
    flex-direction: row;
    position: initial;
  }
}
.header__contenedor .header__menu .header__links.menu-activo {
  visibility: visible;
  opacity: 1;
}
.header__contenedor .header__menu .header__links a {
  font-weight: 500;
  display: inline;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .header__contenedor .header__menu .header__links a {
    font-size: 1.8rem;
  }
}
.header__contenedor .header__menu .header__links a::after {
  content: "";
  width: 2rem;
  display: block;
  padding-top: 0.25rem;
  transition: all 0.3s;
}
.header__contenedor .header__menu .header__links a:hover::after {
  content: "";
  width: 100%;
  display: block;
  font-weight: 700;
  border-bottom: 0.2rem solid white;
}

.main {
  margin-top: 4rem;
  transition: all 0.3s;
  margin-bottom: 15rem;
}
@media (min-width: 768px) {
  .main {
    margin-top: 12rem;
  }
}

@media (min-width: 1024px) {
  .main__container {
    display: grid;
    grid-template-columns: 2fr 3fr;
    align-items: center;
    gap: 2rem;
  }
}
.main__container .main__text .main__title {
  font-family: "Lora", serif;
  font-weight: 200;
  font-size: 4.4rem;
}
.main__container .main__text .main__paragraph {
  margin-top: 3.2rem;
  max-width: 25rem;
  font-size: 2rem;
}
.main__container .main__text .main__buttom {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.main__container .main__text .main__buttom p {
  font-weight: 700;
  font-size: 2.2rem;
}
.main__container .main__picture img {
  margin-top: 2.5rem;
}

.main__picture {
  position: relative;
  margin-bottom: 5rem;
}
.main__picture .main__modal {
  width: 100%;
}

.modal__perfil {
  padding: 2rem;
  width: 80%;
  max-width: 30rem;
  position: absolute;
  top: 85%;
  right: 5%;
  background-color: #181719;
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.modal__perfil .modal__box {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}
.modal__perfil .modal__box .modal__photo {
  width: 4rem;
}
.modal__perfil .modal__box .modal__photo img {
  margin: 0;
  border-radius: 50%;
}
.modal__perfil .modal__description {
  font-family: "Lora", serif;
  font-size: 2rem;
  margin: 0;
}

.modal__box .modal__user .modal__name {
  font-size: 1.4rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .modal__box .modal__user .modal__name {
    font-size: 1.6rem;
  }
}
.modal__box .modal__user .modal__profession {
  font-size: 1.2rem;
  color: #828282;
}
@media (min-width: 768px) {
  .modal__box .modal__user .modal__profession {
    font-size: 1.4rem;
  }
}